    <div class="company-content">
      <!-- 会社名（商号） -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-name") }}</el-col
        >
        <el-col :span="12" :xs="12" class="">
          {{ companyInfo.companyName }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 本社＆拠点 -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-headquarters-and-offices") }}</el-col
        >
        <el-col :span="12" :xs="12" class="">
          {{ companyInfo.headquartersAndOffices }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 海外オフィス -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-overseas-offices") }}</el-col
        >
        <el-col :span="12" :xs="12" class="">
          {{ companyInfo.overseasOffices }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 決算期 -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-fiscal-year") }}</el-col
        >
        <el-col :span="12" :xs="12" class="">
          {{ companyInfo.fiscalYear }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 事業内容 -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-business-content") }}</el-col
        >
        <el-col :span="12" :xs="12" class="">
          {{ companyInfo.businessContent_ }}</el-col
        >
      </el-row>
      <el-divider />
      <!-- 所在地 -->
      <el-row>
        <el-col :span="12" :xs="12" style="font-weight: bold">
          {{ $t("companyInfo-address") }}</el-col
        >
        <el-col :span="12" :xs="12" class=""> {{ companyInfo.address }}</el-col>
      </el-row>
      <el-divider />
    </div>
    <div class="map-container">
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3110.1245340436726!2d141.51003327593128!3d38.7837793717486!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x5f88f1d7dca02e79%3A0x7fe1df2d8d117c44!2s162-1%20Motoyoshich%C5%8D%20Toyomazawa%2C%20Kesennuma%2C%20Miyagi%20988-0318!5e0!3m2!1szh-CN!2sjp!4v1739947948439!5m2!1szh-CN!2sjp"
        width="600"
        height="450"
        style="border: 0"
        allowfullscreen=""
        loading="lazy"
        referrerpolicy="no-referrer-when-downgrade"
        class="google-map"
      ></iframe>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, watch } from "vue";
import { useI18n } from "vue-i18n";
import { db, collection, getDocs } from "../firebase";

export default {
  setup() {
    const { locale } = useI18n();
    // 主区域情报
    const companyInfo = ref({});

    const getCompanyInfo = async () => {
      // const info = {
      //   companyName_ja: "株式会社盛栄国際",
      //   companyName_en: "Seiei International Corporation",
      //   headquartersAndOffices_ja: "仙台、大宮、名古屋、大阪、福岡",
      //   headquartersAndOffices_en: "Sendai, Omiya, Nagoya, Osaka, Fukuoka",
      //   overseasOffices_ja: "香港、カナダ ",
      //   overseasOffices_en: "Hong Kong ,  Canada",
      //   fiscalYear_ja: "3月31日",
      //   fiscalYear_en: "March 31 ",
      //   businessContent_ja: "自動車関連全般",
      //   businessContent_en: "Automobile-related businesses in general",
      //   address_ja: "xxxxxxxxxxx",
      //   address_en: "xxxxxxxxxxx",
      // };
      try {
        const snapshot = await getDocs(collection(db, "companyInfo"));
        const info = snapshot.docs[0].data();

        if (locale.value === "ja") {
          companyInfo.value.companyName = info.companyName_ja;
          companyInfo.value.headquartersAndOffices =
            info.headquartersAndOffices_ja;
          companyInfo.value.overseasOffices = info.overseasOffices_ja;
          companyInfo.value.fiscalYear = info.fiscalYear_ja;
          companyInfo.value.businessContent_ = info.businessContent_ja;
          companyInfo.value.address = info.address_ja;
        } else {
          companyInfo.value.companyName = info.companyName_en;
          companyInfo.value.headquartersAndOffices =
            info.headquartersAndOffices_en;
          companyInfo.value.overseasOffices = info.overseasOffices_en;
          companyInfo.value.fiscalYear = info.fiscalYear_en;
          companyInfo.value.businessContent_ = info.businessContent_en;
          companyInfo.value.address = info.address_en;
        }
      } catch (error) {
        console.error("Error getting documents: ", error);
        throw error;
      }
    };
    onMounted(() => {
      getCompanyInfo();
    });
    watch(locale, () => {
      getCompanyInfo();
    });
    return {
      companyInfo,
    };
  },
};
</script>

